.RightPanel {
  position: relative;
  height: calc(100vh - 50px);
  padding-left: 260px;
  text-align: left;
  padding-top: 20px;
  padding-right: 20px;
  overflow-x: scroll;
}

.RightPanel img {
  width: 400px;
  height: 750px;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

table {
  border-collapse: initial;
  border-spacing: 0px;
}
